@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;1,100;1,200;1,300&display=swap');

.navBarWrapper {
  width: 20vw;
  height: 100vh;
  background-color: #061724;
  color: #7394af;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Poppins", sans-serif;
  justify-content: center;
  z-index: 1;
}

.navBar {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  justify-content: space-evenly;
  padding: 10px;
}
.navBar img {
  border: 7px solid #154c79;
  border-radius: 50%;
  width: 180px;
  height: 180px;
}

.navBar li {
  list-style: none;
  cursor: pointer;
}

.navBar a {
  color: #7394af;
  text-decoration: none;
}
